
export const DEFAULT_THEME = {
    default: 95,
    hint: 90,
    multi: 90,
    param: 96,
    required: 33,
};
/**
 * Wrapper for fixed size tuple values produced by {@link tuple}.
 */
export class Tuple {
    constructor(value) {
        this.value = value;
    }
    deref() {
        return this.value;
    }
}

